import "./Phases.scss";
import Title from "../Title/Title";
import Slider from '../Slider/Slider';
import { useInView } from "react-intersection-observer";

const Phases = ({onCursorLeftArrowEnter, onCursorRightArrowEnter, onCursorDefault}) => {

  const { ref, inView } = useInView({
    threshold: 0.5,
    triggerOnce: true
  });

  
  const onCursorLeftArrowHover = () => {
    onCursorLeftArrowEnter();
  }
  const onCursorRightArrowHover = () => {
    onCursorRightArrowEnter();
  }
  
  const onCursorLeave = () => {
    onCursorDefault();
  }

  const cardsClassList = inView? 'phases__cards animated': 'phases__cards';

  return (
    <section className="phases">

      <div className='phases__layout'>
          <div className='phases__bg'></div>
      </div>

      <div className="phases__content">
        <Title title='     '> </Title>



        <div ref={ref} className={cardsClassList}>
        <svg  className='phases__svg'  viewBox="0 0 1303 192" fill="none" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none">
          <path d="M0 1H1238C1273.35 1 1302 29.6538 1302 65V127C1302 162.346 1273.35 191 1238 191H0" />
        </svg>
          <div className="phases__card phases_active">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title"> 6 </h3>
            <p className="phases__description">      </p>
          </div>

          <div className="phases__card">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title"> 15 </h3>
            <p className="phases__description">   </p>
          </div>

          <div className="phases__card">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title"> 13 </h3>
            <p className="phases__description">  </p>
          </div>

          <div className="phases__card">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title">1 26 </h3>
            <p className="phases__description">  </p>
          </div>

          <div className="phases__card">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title">2 26 </h3>
            <p className="phases__description">  </p>
          </div>

          <div className="phases__card">
            <div className="phases__dot-box">
              <div className="phases__dot"></div>
            </div>
            <h3 className="phases__title">3 26 </h3>
            <p className="phases__description">  </p>
          </div>
        </div>
        <Slider autoPlay      = {false}
                autoPlayTime  = {5000}
                width         = {'100%'}
                height        = {'100%'} 
                onCursorLeftArrowEnter  = {onCursorLeftArrowHover} 
                onCursorRightArrowEnter = {onCursorRightArrowHover} 
                onCursorDefault         = {onCursorLeave}/>
      </div>

    </section>
  )
}

export default Phases;