import './Footer.scss';
import { Link } from 'react-router-dom';

const Footer = ({onCursorButtonEnter, onCursorDefault}) => {


  const onButtonHoverCursor = () => {
    onCursorButtonEnter();
  }
  
  const onCursorLeave = () => {
    onCursorDefault();
  }

 return (
  <footer className='footer'>
    <div className='footer__container'>
      <div className='footer__box'>
        <h4 className='footer__title'> 2022   </h4>
        <Link className='footer__link' 
              to='#'
              onMouseEnter={onButtonHoverCursor} 
              onMouseLeave={onCursorLeave}>
                   </Link>
        <Link className='footer__link' 
              to='#'
              onMouseEnter={onButtonHoverCursor} 
              onMouseLeave={onCursorLeave}>
                 </Link>
      </div>

      <div className='footer__box'>
        <h4 className='footer__title'>   :</h4>
        <p className='footer__text'>   125047, </p>
        <p className='footer__text'>, . , 14/13 ( )</p>
      </div>

      <div className='footer__box'>
        <h4 className='footer__title'> : egurvich@genplanmos.ru</h4>
        <div className='footer__links'>
          <Link className='footer__link' 
              to='#'
              onMouseEnter={onButtonHoverCursor} 
              onMouseLeave={onCursorLeave}>
                Telegram</Link>
          <Link className='footer__link' 
              to='#'
              onMouseEnter={onButtonHoverCursor} 
              onMouseLeave={onCursorLeave}>
                Vkontakte</Link>  
        </div>
      </div>
    </div>
  </footer>
 )
}

export default Footer;