import './Lead.scss';
import React from 'react';
import { Link } from 'react-scroll';


const Lead = ({onCursorButtonEnter, onCursorDefault, isLeadHide}) => {

const onButtonHoverCursor = () => {
  onCursorButtonEnter();
}

const onCursorLeave = () => {
  onCursorDefault();
}

const leadClassList  = isLeadHide? 'lead_hide': 'lead';

  return (
    <section className={leadClassList} >
      <div className='lead__content'>
        
          <div className='lead__title'>
            <h1></h1> 
            <span>
              <p>&nbsp;</p>
              <p></p>   
              <p> <Link smooth={true}  to="form" className='lead__button' onMouseEnter={onButtonHoverCursor} onMouseLeave={onCursorLeave}>  </Link></p>
            </span>
          </div>
          

      
        <div className="lead__startpoint"></div>


        <div className='lead__infoblock'>
          <p className='lead__info'> ,  :</p>
          <p className='lead__prof'>, , , , , , -, </p>
        </div>

      </div>


     
    </section>
  )
}

export default Lead;